const mapDispatchToProps = (dispatch, ownProps) => {
  return bindActionCreators(
    {
      saveDataToCart: Actions.saveDataToCart,
      saveProductToShortlist: Actions.saveProductToShortlist,
      showResponseMessage: Actions.showResponseMessage,
    },
    dispatch
  );
};

export default withReducer(
  "cartSidebar",
  reducer
)(connect(null, mapDispatchToProps)(ProductCart));
