import React, { Component } from "react";
import { Row, Col, Nav, Tab } from "react-bootstrap";
import _ from "lodash";

import { getCollectionAttribute } from "./AttributeUtility";

class VariantAttributes extends Component {
  constructor(props) {
    super(props);

    this.state = {
      variants: null,
      activeTab: 0,
    };
  }

  static getDerivedStateFromProps(props, state) {
    return {
      variants: props.variants,
      size: props.size,
      color: props.color,
      age: props.age,
    };
  }

  componentWillUnmount() {
    this.setState({
      color: null,
      age: null,
      size: null,
    });
  }

  handleColor = (event) => {
    // console.log('Color :: ' + event.target.getAttribute('color'));

    event.target.getAttribute("color")
      ? this.props.handleColor(event.target.getAttribute("color"))
      : this.props.handleColor(this.props.color);
  };

  handleSize = (event) => {
    console.log("Size :: " + event.target.getAttribute("sizeName"));

    event.target.getAttribute("sizeName")
      ? this.props.handleSize(event.target.getAttribute("sizeName"))
      : this.props.handleSize(this.props.size);
  };

  handleAge = (event) => {
    console.log("Age :: " + event.target.getAttribute("age"));

    event.target.getAttribute("age")
      ? this.props.handleAge(event.target.getAttribute("age"))
      : this.props.handleAge(this.props.age);
  };

  render() {
    let attributes = getCollectionAttribute(
      this.state.variants && this.state.variants
    );
    let colorAttributes = _.filter(attributes, (att) => !!att.Color);
    let sizeAttributes = _.filter(attributes, (att) => !!att.Size);
    let ageAttributes = _.filter(attributes, (att) => !!att.Age);

    return (
      <>
        <Tab.Container
          className="allsizes"
          defaultActiveKey="1"
          activeKey={this.state.activeTab}
        >
          <div className="pro-choose">
            {!!colorAttributes.length && (
              <div className="cline">
                <p className="web">CHOOSE COLOR</p>
                <p className="mob">COLOR</p>
                <Nav bsStyle="tabs">
                  {/* {
                                    this.state.product.Type === 'varient' ? this.state.product.Varients.map((vari, key) => (
                                        vari.Attributes[1].Colors.map((color) => ( */}

                  {_.find(attributes, (att) => {
                    return att["Color"];
                  }) ? (
                    attributes.map((att, key) => {
                      if (att["Color"]) {
                        return (
                          <span className="bg-color-border web-border">
                            <Nav.Link
                              className="bg-color"
                              style={{ background: `${att["Color"]}` }}
                              color={att["Color"]}
                              onClick={this.handleColor}
                              onMouseOver={() => {
                                this.setState({ activeTab: key });
                              }}
                              onMouseOut={() => {
                                this.setState({ activeTab: key });
                              }}
                              eventKey={key}
                            >
                              <span
                                className="mob-border"
                                style={{ border: `1px solid ${att["Color"]}` }}
                              ></span>
                            </Nav.Link>
                          </span>
                        );
                      } else {
                        return <></>;
                      }
                    })
                  ) : (
                    <div className="bg-color-border">
                      <Nav.Link
                        className="bg-color"
                        style={{ background: `white` }}
                        color={`white`}
                        // onClick={this.handleColor}
                        onMouseOver={() => {
                          this.setState({ activeTab: 1 });
                        }}
                        onMouseOut={() => {
                          this.setState({ activeTab: 1 });
                        }}
                        eventKey={0}
                      ></Nav.Link>
                    </div>
                  )}

                  {/* {this.state.variants && this.state.variants.map((variant) => {
                                return(variant['Attributes'].map((att) => {
                                        if(att.cat === 'Color')
                                        {
                                            return(
                                                <div className="bg-color-border">                                        
                                                    <Nav.Link
                                                        className="bg-color"
                                                        style={{ background: `${att.value}` }}
                                                        onMouseOver={() => { this.setState({ activeTab: 0 }) }}
                                                        onMouseOut={() => { this.setState({ activeTab: 0 }) }}
                                                        eventKey={0}>
                                                    </Nav.Link>
                                                </div>
                                                )
                                        }
                                }))
                            })} */}
                </Nav>
              </div>
            )}
            {!!sizeAttributes.length && (
              <div className="cline">
                <p className="web">CHOOSE SIZE</p>
                <p className="mob">SIZE</p>
                <Tab.Content activeTab={this.state.activeTab} animation>
                  <Tab.Pane eventKey={this.state.activeTab}>
                    <Row className="clearfix">
                      <Col lg={12} sm={12} className="size-list">
                        <span>
                          {attributes.map((att) => {
                            if (att["Size"]) {
                              return (
                                <a
                                  href="###"
                                  sizeName={att["Size"]}
                                  onClick={this.handleSize}
                                >
                                  {att["Size"]}
                                </a>
                              );
                            } else {
                              return <></>;
                            }
                          })}
                        </span>
                      </Col>
                    </Row>
                  </Tab.Pane>
                </Tab.Content>
              </div>
            )}

            {!!ageAttributes.length && (
              <div className="cline">
                <p className="web">CHOOSE AGE</p>
                <p className="mob">AGE</p>
                <Tab.Content activeTab={this.state.activeTab} animation>
                  <Tab.Pane eventKey={this.state.activeTab}>
                    <Row className="clearfix">
                      <Col lg={12} sm={12} className="size-list">
                        <span>
                          {attributes.map((att) => {
                            if (att["Age"]) {
                              return (
                                <a
                                  href="##"
                                  age={att["Age"]}
                                  onClick={this.handleAge}
                                >
                                  {att["Age"]}
                                </a>
                              );
                            } else {
                              return <> </>;
                            }
                          })}
                        </span>
                      </Col>
                    </Row>
                  </Tab.Pane>
                </Tab.Content>
              </div>
            )}

            <div className="selected">
              <p>Your Selection</p>
              <div className="pro-selected">
                <div
                  className="bg-color"
                  style={{ background: this.state.color }}
                ></div>
                <p>{this.state.size}</p>
                <p>{this.state.age}</p>
              </div>
            </div>
          </div>
        </Tab.Container>
      </>
    );
  }
}

export default VariantAttributes;

