import React, { Component } from "react";

import Product from "./../../product.json";

// ... COMPONENTS ...
import Breadcrumb from "../breadCrumb/Breadcrumb";
import SimpleProduct from "./SimpleProduct";
import VariantProduct from "./VariantProduct";

// ... REDUX ...
import withReducer from "../../store/withReducer";
import connect from "react-redux/es/connect/connect";
import { bindActionCreators } from "redux";
import reducer from "../store/reducers";
import * as Actions from "../store/actions";
import asyncComponent from "../../AsyncComponent";
import { hostUrl } from "../store/config";

import { Helmet } from "react-helmet";
class ProductDetailsPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activeTab: 0,
      product: Product.data,
      product_detail: null,
      brand: this.props.match.params.brand,
      slug: this.props.match.params.slug,
      id: this.props.match.params.id,
      nav1: null,
      nav2: null,
      value: 1,
      rating: 1,
      userfilter: "user",
      // category:'Baby-Clothes',
      // subCategory: 'Shalwaar',
    };
  }

  hoverImgEnter = () => {
    this.refs.proZoom.style.display = "block";
  };

  hoverImgLeave = () => {
    this.refs.proZoom.style.display = "none";
  };

  increase = () => {
    this.setState({ value: this.state.value + 1 });
  };

  decrease = () => {
    if (this.state.value) {
      this.setState({
        value: this.state.value - 1,
      });
    }
  };

  componentDidMount() {
    // console.log('Parameter SKU :: ' + this.props.match.params.slug);
    this.props.getProductSEO(this.props.match.params.slug);
    this.props.getProduct(this.props.match.params.slug);
    this.setState({
      nav1: this.slider1,
      nav2: this.slider2,
      //   product:null,
      //   product_detail:null
    });
  }

  //   componentWillUnmount() {
  //       console.log('Unmounted')
  //     this.setState({
  //         nav1: this.slider1,
  //         nav2: this.slider2,
  //         product:null,
  //         product_detail:null
  //       });
  //   }

  static getDerivedStateFromProps(props, state) {
    // props.product_detail && console.log('Product Details :: ' + JSON.stringify(props.product_detail))
    // props.product_seo && console.log('Product SEO :: ' + JSON.stringify(props.product_seo))
    return {
      product_detail: props.product_detail,
      slug: props.match.params.slug,
      product_seo: props.product_seo,
      category_of_product: props.category_of_product,
    };
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevProps.match.params.slug !== this.props.match.params.slug) {
      // console.log('state slug :: ' + prevProps.match.params.slug)
      // console.log('props slug :: ' + this.props.match.params.slug)
      this.props.getProductSEO(this.props.match.params.slug);
      this.props.getProduct(this.props.match.params.slug);
    }
  }

  render() {
    var setting = {
      responsive: [
        {
          breakpoint: 1024,
          settings: {
            slidesToShow: 3,
            slidesToScroll: 3,
            infinite: true,
          },
        },
        {
          breakpoint: 600,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1,
            initialSlide: 1,
            infinite: true,
          },
        },
        {
          breakpoint: 480,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1,
            infinite: true,
          },
        },
      ],
    };

    // TODO: we need to render the Helmet on product Component
    const product_name =
      this.state.product_seo && this.state.product_seo.product_seo_title
        ? this.state.product_seo.product_seo_title
        : "Online Shopping";
    const product_tags =
      this.state.product_seo &&
      this.state.product_seo.product_tags &&
      this.state.product_seo.product_tags;
    const product_desc =
      this.state.product_seo &&
      this.state.product_seo.product_seo_desc &&
      this.state.product_seo.product_seo_desc;
    return (
      <>
        <Helmet>
          <title>{`${product_name} - Baby Nest Boutique`}</title>
          <meta
            data-react-helmet="true"
            name="description"
            content={product_desc}
          />
          <meta
            data-react-helmet="true"
            name="keywords"
            content={product_tags}
          />
        </Helmet>
        <section>
          <Breadcrumb
            parentName={
              this.state.category_of_product
                ? this.state.category_of_product.parent_name
                : ""
            }
            subParentName={
              this.state.category_of_product
                ? this.state.category_of_product.cat_name
                : ""
            }
            Title={
              (this.state.product_detail &&
                this.state.product_detail["Name"]) ||
              ""
            }
          />

          {this.props.product_detail &&
          this.props.product_detail["Type"] === "simple" ? (
            // <p>Simple Prodcuct</p>:
            <SimpleProduct
              simpleProduct={
                this.state.product_detail && this.state.product_detail
              }
              addToCart={this.props.saveDataToCart}
              category_of_product={this.state.category_of_product}
            />
          ) : (
            <VariantProduct
              variantProduct={
                this.state.product_detail && this.state.product_detail
              }
              addToCart={this.props.saveDataToCart}
              nav1={this.state.nav1}
              nav2={this.state.nav2}
              category_of_product={this.state.category_of_product}
            />
          )}
        </section>
      </>
    );
  }
}

const mapStateToProps = (state, ownProps) => {
  return {
    saveDataToCart: Actions.saveDataToCart,
    product_detail: state.productdetail.product.product_detail,
    product_seo: state.productdetail.product.product_seo,
    category_of_product: state.productdetail.product.category_of_product,
  };
};

const mapDispatchToProps = (dispatch, ownProps) => {
  return bindActionCreators(
    {
      getProduct: Actions.getProduct,
      getProductSEO: Actions.getProductSEO,
    },
    dispatch
  );
};

export default withReducer(
  "productdetail",
  reducer
)(connect(mapStateToProps, mapDispatchToProps)(ProductDetailsPage));

