import React, { Component } from 'react'
import Slider from "react-slick";
import { hostUrl } from '../../../store/config';
import {Link} from 'react-router-dom'
import Product from "../../../../product.json";

// ... REDUX ...
import withReducer from "../../../../store/withReducer";
import connect from "react-redux/es/connect/connect";
import { bindActionCreators } from "redux";
import reducer from "../../../store/reducers";
import * as Actions from "../../../store/actions";
import { fromEventPattern } from 'rxjs';

class SuggestedProductSlider extends Component {

    constructor(props)
    {
        super(props);

        this.state={
            product: Product.data,
            similar_products: []
        }
    }


    static getDerivedStateFromProps(props, state) {
        // console.log('Props product :: ' + JSON.stringify(props.similar_products));
        return {
            similar_products: props.similar_products
        }
    }

    render() {

        var setting ={
            responsive: [
                {
                  breakpoint: 1400,
                  settings: {
                    slidesToShow: 4,
                    slidesToScroll: 4,
                    infinite: true,
                  }
                },
                {
                  breakpoint: 1024,
                  settings: {
                    slidesToShow: 3,
                    slidesToScroll: 3,
                    infinite: true,
                  }
                },
                {
                  breakpoint: 600,
                  settings: {
                    slidesToShow: 2,
                    slidesToScroll: 2,
                    initialSlide: 2,
                    infinite: true,
                    dots: true,
                    arrows: false,
                  }
                },
                {
                  breakpoint: 480,
                  settings: {
                    slidesToShow: 2,
                    slidesToScroll: 2,
                    infinite: true,
                    dots: true,
                    arrows: false,
                  }
                }
              ]
        }

        let similar_products = [];
        if(this.state.similar_products)
        similar_products = this.state.similar_products;

        return (
            <>
                <div className="brand-ads">
                    <h2 className="active">YOU MAY ALSO LIKE</h2>
                </div>
                <div className="slider">
                    <Slider
                        {...setting}
                        infinite={true}
                        speed={500}
                        slidesToScroll= {1}
                        slidesToShow={6}
                        swipeToSlide={true}
                        focusOnSelect={true}
                        arrows={true}
                        dots={false}
                        pauseOnHover={true}
                        autoplay={true}
                    >
                        { similar_products &&
                            similar_products.map((pro) => (
                            <div>
                                <div className="brand-ads-slider">
                                    <div className="pro-item">
                                        <Link to={`/product/${pro && pro['Slug']}`} replace>
                                            <div className="pro-detail">
                                                <div className="pro-img">
                                                    <img width='100%' src={`${hostUrl}${pro.Image.split(',')[0]}`} alt="" />
                                                </div>
                                                <div className="pro-txt">
                                                    <p>{pro.Name} </p>
                                                </div>
                                                <div className="pro-price">
                                                    {
                                                        pro["Sale Price"] === 0 ? 
                                                        <span className="price">Rs.{pro.Price}</span> 
                                                    :
                                                        <>
                                                        <span className="price">Rs. {pro["Sale Price"]}
                                                        </span>
                                                        <span className="o-price">Rs. {pro.Price}
                                                        </span>
                                                        <span className="disc">(UPTO {Math.round(((pro['Price'] - pro['Sale Price']) / pro['Price'] * 100))})</span>
                                                        </>
                                                    }
                                                </div>
                                            </div>
                                        </Link>
                                    </div>
                                </div>
                            </div>
                            ))
                        } 
                    </Slider>
                </div>
            </>
        )
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        similar_products: state.similarProduct.product.similar_products
    }
}

export default withReducer('similarProduct', reducer)
            (
                connect(mapStateToProps)(SuggestedProductSlider)
            )